.toplevel {
    position: relative;
    background-color: #171717;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
}

.full__page {
    width: 100vw;
    height: 100vh;
}

.bar__container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    z-index: 2;
    justify-content: space-between;

    // width: 100%;
    // height: 100%;
    // position: fixed;

    &__inverted {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        z-index: 2;
    }
}

// .bar__container__inverted {
//     width: 100%;
//     height: 100%;
//     position: fixed;
// }

.bar {
    height: 1vh;
    background-color: rgba(255, 0, 0, 0.4);
}

// pointer-events 是一個滿有趣的 CSS3 屬性，雖然主要是針對 SVG ，但其中幾個屬性應用在 div 上也是頗有意思。
// 顧名思義，這是一個針對滑鼠事件的屬性，預設值為 auto，若值為 none，則可以穿越該元素，點擊到下方的元素。
.animated__layers {
    width: 100%;
    height: 100%;
    position: fixed;
    inset: 0;
    pointer-events: none;
    z-index: 0;

    // 後面的所有子元素都會被設定為 position: fixed
    & > * {
        // pointer-events: none;
        width: 100%;
        height: 100%;
        position: fixed;
        inset: 0;
    }
}

.dot {
    background-color: orange;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.title {
    color: rgb(5, 74, 238);
    font-size: 8vw;
    padding-left: 8vw;

    & > span {
        display: block;
        overflow: hidden;

        & > span {
            display: block;
        }
    }
}

.name {
    z-index: 3;
}
